* {
	margin: 0px;
	padding: 0px;
	box-sizing: border-box;
	user-select: none;
	-webkit-tap-highlight-color: transparent;
}

.App {
	height: 100dvh;
	overflow: hidden;
	background-image: linear-gradient(to right, #e68a92, #e6a7a2, #e6c2b1, #c8d9b3, #9cc9c2, #a9b3d1);
	animation: bgColorAnimation infinite 10s alternate-reverse linear;
	background-repeat: no-repeat;
	background-size: 20cm;
	background-size: 200%;
}

@keyframes bgColorAnimation {
	0% {
		background-position-x: left;
	}

	100% {
		background-position-x: right;
	}
}
